import React, { Component } from 'react';
import styled from 'styled-components';
import uuidv1 from 'uuid/v1';
import Balance from './Balance/Balance';
import Controls from './Controls/Controls';
import TransactionHistory from './TransactinoHistory/TransactionHistory';

const operations = {
  DEPOSIT: 'Deposit',
  WITHDRAW: 'Withdraw',
};

const Container = styled.div`
  width: 520px;
  margin: 20px auto;
`;

export default class Dashboard extends Component {
  state = {
    history: [],
    balance: 3000,
  };

  componentDidMount() {
    const localStorageHistory = JSON.parse(localStorage.getItem('history'));

    if (localStorageHistory) {
      this.setState({ history: localStorageHistory });
    }
  }

  componentDidUpdate(prevProps, prevState) {
    const { history } = this.state;

    if (prevState.history !== history) {
      localStorage.setItem('history', JSON.stringify(history));
    }
  }

  addTransaction = (value, type) => {
    const transaction = {
      id: uuidv1(),
      type,
      amount: value,
      date: new Date().toLocaleString(),
    };

    if (type === operations.DEPOSIT) this.plusBalance(value);
    if (type === operations.WITHDRAW) this.minusBalance(value);

    this.setState(state => ({ history: [...state.history, transaction] }));
  };

  minusBalance = value => {
    this.setState(prevState => ({
      balance: prevState.balance - value,
    }));
  };

  plusBalance = value => {
    this.setState(prevState => ({
      balance: prevState.balance + value,
    }));
  };

  countBalanceByType = (history, type) =>
    history.reduce(
      (acc, item) => (acc += item.type === type ? item.amount : 0),
      0,
    );

  render() {
    const { history } = this.state;
    const { balance } = this.state;
    const profitBalance = this.countBalanceByType(history, operations.DEPOSIT);
    const expenseBalance = this.countBalanceByType(
      history,
      operations.WITHDRAW,
    );

    return (
      <Container>
        <Controls addTransaction={this.addTransaction} balance={balance} />
        <Balance
          balance={balance}
          profit={profitBalance}
          expense={expenseBalance}
        />
        <TransactionHistory items={history} />
      </Container>
    );
  }
}
